<template>
  <div>
    <el-table :data="list" v-loading="listLoading"
              element-loading-text="加载中..."
              border
              highlight-current-row>
      <el-table-column label="ID" align="center" width="80">
        <template slot-scope="scope">{{scope.$index}}</template>
      </el-table-column>

      <el-table-column label="Title">
        <template slot-scope="scope">{{scope.row.title}}</template>
      </el-table-column>

      <el-table-column label="Author" align="center" width="100">
        <template slot-scope="scope">{{scope.row.author}}</template>
      </el-table-column>

      <el-table-column label="Pageviews" align="center" width="100">
        <template slot-scope="scope">{{scope.row.pageviews}}</template>
      </el-table-column>

      <el-table-column label="Status" align="center" width="100">
        <template slot-scope="scope">
          <el-tag :type="scope.row.status | statusFilter">{{scope.row.status}}</el-tag>
        </template>
      </el-table-column>

      <el-table-column label="DisplayTime" align="center" width="200">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.display_time}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import {getList} from "@/api/table";

  export default {
    name: "Table",
    data(){
      return {
        list: null,
        listLoading: true
      }
    },
    filters: {
      statusFilter(status) {
        const statusMap = {
          published: 'success',
          draft: 'gray',
          deleted: 'danger'
        }
        return statusMap[status]
      }
    },
    created(){
      this.fetchData()
    },
    methods: {
      fetchData(){
        this.listLoading = true
        getList().then(response => {
          this.list = response.data.list
          this.listLoading = false
        })
      }
    }
  }
</script>

